h1,
h2,
h3 {
    font-family: Ubuntu;
}

h2 {
    font-size: 2.3rem;
    font-weight: normal;
    text-align: center;
}

body {
    font-family: OpenSans;
}

.container {
    width: 1200px;
    margin: 0 auto;
    height: 100%;
}

.background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

.section {
    position: relative;
}

#stat {
    .background {
        display: flex;
        .blue,
        .orange {
            height: 100%;
            width: 50%;
        }
        .blue {
            background: rgb(250, 250, 250);
            background: -moz-linear-gradient(left, rgba(250, 250, 250, 1) 0%, rgba(250, 250, 250, 1) 99.2%, rgba(66, 133, 244, 1) 100%);
            background: -webkit-linear-gradient(left, rgba(250, 250, 250, 1) 0%, rgba(250, 250, 250, 1) 99.2%, rgba(66, 133, 244, 1) 100%);
            background: linear-gradient(to right, rgba(250, 250, 250, 1) 0%, rgba(250, 250, 250, 1) 99.2%, rgba(66, 133, 244, 1) 100%);
        }
    }
    .container {
        display: flex;
        flex-flow: row nowrap;
        position: relative;
        h2 {
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
        }
        .metrika,
        .pagespeed {
            width: 50%;
            display: flex;
            align-items: center;
        }
        .metrika {
            justify-content: flex-start;
        }
        .center {
            width: 32.1rem;
            height: 28rem;
        }
        .pagespeed {
            h3 {
                margin: 1.2rem 0;
            }
            font-family: Roboto;
            font-size: 1rem;
            justify-content: flex-end;
            padding-right: 2rem;
            a {
                color: #039be5;
            }
            .top {
                display: flex;
                margin-top: 1.5rem;
                margin-bottom: 1rem;
                .result {
                    margin-right: 3.5rem;
                    display: flex;
                    align-items: center;
                }
                .name {
                    margin-left: .6rem;
                    font-size: 1.6rem;
                    text-align: center;
                }
                .value {
                    font-family: OpenSans;
                    padding: 0.2rem 0.6rem;
                    text-align: center;
                    font-size: 1.6rem;
                    color: white;
                    background: #009a2b;
                }
            }
            .disclaimer {
                border-top: 1px solid #ccc;
                padding-top: .5rem;
                margin-top: 2rem;
                margin-right: -1.8rem;
                ;
            }
            .rules {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-bottom: 1rem;
                .text {
                    font-size: 1.1rem;
                    font-weight: bold;
                }
                .icon {
                    padding: .4rem 0;
                    color: white;
                    font-size: 1.3rem;
                    margin-right: .4rem;
                }
                .icon:before {
                    margin-left: .1rem;
                    margin-right: .1rem;
                }
            }
            .passed.rules .icon {
                background: #009a2b;
            }
            .nonpassed.rules .icon {
                background: #fda100;
            }
            span {
                color: #776;
                font-style: italic;
            }
            .line {
                margin-left: -100rem;
                margin-right: -1.9rem;
                box-shadow: 0 0 4px rgba(0, 0, 0, .14), 0 4px 8px rgba(0, 0, 0, .28);
                background: #4285f4;
                color: white;
                padding-right: 20rem;
                display: flex;
                justify-content: flex-end;
            }
        }
        .metrika {
            .center {
                padding-left: 1rem;
            }
            .line {
                height: 3.8rem;
                .link {
                    display: flex;
                }
                h3 {
                    margin: 1rem 0;
                    font-weight: normal;
                    font-size: 1rem;
                    line-height: 1.5;
                }
                .yandex-logo-1 {
                    margin-top: .35rem;
                    width: 6rem;
                    height: 2.6rem;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-image: url(data:image/svg+xml;base64,);
                }
                .yandex-logo-2 {
                    position: absolute;
                    top: .35rem;
                    left: 1.8rem;
                    margin-top: .6rem;
                    width: 6rem;
                    height: 2.6rem;
                    font-family: YandexSansText, Arial, Helvetica, sans-serif;
                    font-size: 1.45em;
                    font-weight: bold;
                    color: #000 !important;
                }
            }
            .yellow-box-wrapper {
                position: relative;
            }
            .yellow-box {
                width: 12rem;
                height: 3.8rem;
            }

            .charts {
                margin-top: 2rem;
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                .chart {
                    width: 15rem;
                    padding: 1rem;
                    border: 1px solid #e5e5e5;
                    margin-bottom: 1.1rem;
                }
            }
        }
    }
}